.link-buttons
{   
    font-family: 'DM Sans', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.045em;
    width: 100px;
    height: 48px;
    /* Inside Auto Layout */
    margin: 0px 20px;

    /* style */
    background: none;
    color: #fff;
    border: 0;
    outline: none;
    cursor: pointer;
    text-transform: uppercase;
    border-radius: 8px;
}

.navi-cons{
    display: inline;
}


.hamburger_container {
    display: none;
}

.hamburger .line{
    width: 35px;
    height: 3px;
    background-color: #ecf0f1;
    display: block;
    margin: 8px auto;
    transition: all 0.3s ease-in-out;
  }

#hamburger-12.is-active .line:nth-child(1){
    opacity: 0;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
  
#hamburger-12.is-active .line:nth-child(3){
    opacity: 0;
    -webkit-transform: translateX(100%);
    transform: translateX(100%);
}

.three { width: 23.8%; }

.col {
	display: block;
	float:left;
	margin: 1% 0 1% 1.6%;
}

.col:first-of-type {
    margin-left: 0;
  }

.link-buttons:hover
{
    background: #202020;
    color: #3C95FF;
}

.activated
{
    color: #3C95FF;
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 0.15em;
    
    display: inline-block;
    padding: 15px 20px;
    position: relative;
}

.activated:after
{
    display:block;
  content: '';
  border-bottom: solid 2px #3C95FF;  
  -webkit-transform: scaleX(1);  
          transform: scaleX(1);  
  transition: -webkit-transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}

.active {
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 0.15em;
    display: inline-block;
    position: relative;
  }

  
 .active:after {
    display:block;
  content: '';
  border-bottom: solid 2px #3C95FF;  
  -webkit-transform: scaleX(0);  
          transform: scaleX(0);  
  transition: -webkit-transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
  }

  .active:hover:after { 
    -webkit-transform: scaleX(0.8); 
            transform: scaleX(0.8);  
  }


