}


.radialProgressBar {
    bottom: 100px; right: 100px;
    border-radius: 50%;
    -webkit-transform: translate(50%, 50%);
            transform: translate(50%, 50%);
    width: 105px;
    height: 105px;
    display: flex;
    background: #131313;
    margin: 20px;
}
.radialProgressBar .overlay {
    border-radius: 50%;
    width: 100px;
    height: 100px;
    margin: auto;
