@import url('https://fonts.googleapis.com/css2?family=DM+Sans&display=swap');

.work_title
{
    font-style: normal;
font-weight: bold;
font-size: 64px;
line-height: 72px;
/* identical to box height, or 112% */ 
margin-bottom: 4rem;
color: #FFFFFF;
}


.experience_container{
    font-family: 'DM Sans', sans-serif;
    width: fit-content;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    margin: 0 auto;
}


.title_ender {
    position: relative;
    top: -20px;
    left: 4px;
}

.timeline{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-end;
}

.year
{
    font-size: 32px;
    font-weight: 800;
    background: #202020;
    width: fit-content;
    height: fit-content;
    padding: 15px 15px;
    border-radius: 8px;
}

.timeline_img{
    margin-top: 50px;
    max-width: 1150px;
}

.timeline_second{
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

@media screen and (max-width: 1200px) {
    .experience_container
    {
        width: 60%;
    }

    .timeline_img{
        margin-top: 25px;
        max-width: 1150px;
    }

    .year{
        font-size: 24px;
    }

}

